import PopoverMenu from '@nextcloud/vue/dist/Components/PopoverMenu'

export default {
	components: {
		PopoverMenu
	},
	data() {
		return {
			menuOpened: false
		}
	},
	methods: {
		togglePopoverMenu() {
			this.menuOpened = !this.menuOpened
		},
		hidePopoverMenu() {
			this.menuOpened = false
		}
	}
}
