<template>
	<div class="emptycontent">
		<img v-if="item.image"
			:src="imageUrl"
			class="icon-illustration"
			alt="">
		<h2>{{ item.title }}</h2>
		<p>{{ item.description }}</p>
	</div>
</template>

<script>

export default {
	name: 'EmptyContent',
	props: {
		item: { type: Object, default: () => {} }
	},
	computed: {
		imageUrl() {
			return OC.linkTo('social', this.item.image)
		}
	}
}
</script>
<style scoped>
	.emptycontent {
		margin-top: 5vh;
	}

	.emptycontent .icon-illustration {
		height: 256px;
		width: 256px;
		margin: 0;
		opacity: 1;
	}
</style>
